import React from 'react';

import logo_text from '../../images/logo_text_black.png';

import "./AboutUs.css";

const functions = [
  "Oficera Bezpieczestwa",
  "Penomocnika do spraw ochrony informacji niejawnych",
  "Administratora bezpieczestwa informacji",
  "Inspektora bezpieczestwa teleinformatycznego",
  "Inspektora bezpieczestwa fizycznego"
];

const AboutUs = () => (
  <div className="about-us">
    <h2>
      <img
        className="about-us__image"
        src={logo_text}
        alt="Kontecho - Ochrona Danych"
      />
    </h2>
    <div className="about-us__text">
      <h3>Ochron informacji i zabezpieczeniami zajmuj si od ponad 10 lat.</h3>
      <div className="about-us__functions">
        <strong>Dotychczasowe dowiadczenie zdobyem w obszarze administracji rzdowej i biznesie penic funkcje:</strong>
        <div>
          {functions.map((title, key) => (
            <span key={key}>
              {title}
            </span>
          ))}
        </div>
      </div>
      <div className="about-us__certificates">
        Posiadam niezbdne szkolenia i certyfikaty.
      </div>
    </div>
  </div>
);

export default AboutUs;



